import React, { useState, useEffect, useCallback } from 'react';
import Tesseract from 'tesseract.js';
import { toast } from 'react-hot-toast';

interface ExtractedRegion {
  id: string;
  name: string;
  text: string;
  confidence?: number;
}

interface OCRResult {
  fullText: string;
  regions: ExtractedRegion[];
  confidence: number;
}

interface OCRProcessorProps {
  image: File;
  onOCRComplete: (result: OCRResult) => void;
  onProcessingStart: () => void;
  onProcessingEnd: () => void;
  extractedText?: string;
  fileType?: string;
  template?: any; // 
}

const OCRProcessor: React.FC<OCRProcessorProps> = ({
  image,
  onOCRComplete,
  onProcessingStart,
  onProcessingEnd,
  extractedText,
  fileType,
  template
}) => {
  const [fullText, setFullText] = useState<string>('');
  const [ocrProgress, setOcrProgress] = useState<number>(0);
  const [isProcessing, setIsProcessing] = useState<boolean>(false);
  const [editableText, setEditableText] = useState<string>('');
  const [extractedRegions, setExtractedRegions] = useState<ExtractedRegion[]>([]);

  // OCR
  const performOCR = useCallback(async () => {
    // PDF
    if (fileType === 'application/pdf' && extractedText) {
      // PDF
      if (extractedText.trim().length > 0) {
        setFullText(extractedText);
        setEditableText(extractedText);
        processWithTemplate(extractedText);
        toast.success('PDF');
        return;
      } else {
        // PDF
        toast.error('PDFPDF');
        return;
      }
    }

    // PDF
    if (fileType === 'application/pdf' && !extractedText) {
      toast.error('PDFPDF');
      return;
    }

    // OCR
    if (!fileType?.startsWith('image/')) {
      return;
    }

    setIsProcessing(true);
    onProcessingStart();
    setOcrProgress(0);

    try {
      // OCR
      const worker = await Tesseract.createWorker(['ita', 'eng', 'chi_sim'], 1, {
        logger: m => {
          if (m.status === 'recognizing text') {
            setOcrProgress(Math.round(m.progress * 100));
          }
        }
      });

      const { data: { text, confidence } } = await worker.recognize(image);
      
      await worker.terminate();
      
      setFullText(text);
      setEditableText(text);
      processWithTemplate(text, confidence);
      
      toast.success('OCR');
    } catch (error) {
      console.error('OCR:', error);
      toast.error('OCR');
    } finally {
      setIsProcessing(false);
      onProcessingEnd();
    }
  }, [image, onProcessingStart, onProcessingEnd, extractedText, fileType]);

  //  - 
  const processWithTemplate = useCallback((text: string, confidence: number = 95) => {
    const regions: ExtractedRegion[] = [];
    
    if (template?.regions && template.regions.length > 0) {
      // 
      template.regions.forEach((regionDef: any, index: number) => {
        let extractedText = '';
        
        // 
        if (regionDef.keywords && regionDef.keywords.length > 0) {
          const lines = text.split('\n');
          for (const line of lines) {
            for (const keyword of regionDef.keywords) {
              if (line.toLowerCase().includes(keyword.toLowerCase())) {
                // 
                extractedText = line.trim();
                break;
              }
            }
            if (extractedText) break;
          }
        }
        
        regions.push({
          id: regionDef.id || `region_${index}`,
          name: regionDef.name || ` ${index + 1}`,
          text: extractedText,
          confidence: confidence
        });
      });
    } else {
      // 
      regions.push({
        id: 'full_text',
        name: '',
        text: text,
        confidence: confidence
      });
    }

    setExtractedRegions(regions);
    
    const result: OCRResult = {
      fullText: text,
      regions: regions,
      confidence: confidence
    };
    
    onOCRComplete(result);
  }, [template, onOCRComplete]);

  // 
  const handleTextReparse = useCallback(() => {
    if (editableText.trim()) {
      processWithTemplate(editableText);
      toast.success('');
    }
  }, [editableText, processWithTemplate]);

  // OCR
  useEffect(() => {
    if (image && !isProcessing && !fullText) {
      performOCR();
    }
  }, [image, isProcessing, fullText, performOCR]);

  // 
  const handleRegionEdit = useCallback((regionId: string, newText: string) => {
    const updatedRegions = extractedRegions.map(region => 
      region.id === regionId ? { ...region, text: newText } : region
    );
    setExtractedRegions(updatedRegions);
    
    const result: OCRResult = {
      fullText: fullText,
      regions: updatedRegions,
      confidence: 95
    };
    
    onOCRComplete(result);
  }, [extractedRegions, fullText, onOCRComplete]);

  return (
    <div className="bg-white rounded-lg border border-gray-200 shadow-sm">
      <div className="p-4 border-b border-gray-200">
        <div className="flex items-center justify-between">
          <h3 className="text-lg font-medium text-gray-900"></h3>
          {!isProcessing && fullText && (
            <button
              onClick={performOCR}
              className="text-sm text-blue-600 hover:text-blue-700 transition-colors"
            >
              
            </button>
          )}
        </div>
      </div>

      <div className="p-4">
        {/*  */}
        {fileType && (
          <div className="mb-4 p-3 bg-gray-50 rounded-lg">
            <p className="text-sm text-gray-700">
              <span className="font-medium"></span>
              {fileType === 'application/pdf' ? 'PDF' : ''}
            </p>
            {fileType === 'application/pdf' && extractedText && (
              <div className="mt-2">
                <p className="text-sm text-green-600"> PDF</p>
                <p className="text-xs text-gray-500">
                  : {extractedText.length} 
                </p>
                {extractedText.length === 0 && (
                  <p className="text-xs text-red-600 mt-1">
                     PDF
                  </p>
                )}
              </div>
            )}
            {fileType === 'application/pdf' && !extractedText && (
              <p className="text-sm text-red-600 mt-1"> PDF</p>
            )}
            {template && (
              <p className="text-sm text-blue-600 mt-1">
                 {template.originalname || ''}
                {template.regions && ` (${template.regions.length} )`}
              </p>
            )}
            <p className="text-sm text-purple-600 mt-1">
               
            </p>
          </div>
        )}

        {/* OCR */}
        {isProcessing && (
          <div className="mb-6">
            <div className="flex justify-between items-center mb-2">
              <span className="text-sm text-gray-600">...</span>
              <span className="text-sm text-gray-600">{ocrProgress}%</span>
            </div>
            <div className="w-full bg-gray-200 rounded-full h-2">
              <div
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${ocrProgress}%` }}
              ></div>
            </div>
          </div>
        )}

        {/*  */}
        {fullText && (
          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              
            </label>
            <textarea
              value={editableText}
              onChange={(e) => setEditableText(e.target.value)}
              className="w-full h-32 p-3 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              placeholder="..."
            />
            <button
              onClick={handleTextReparse}
              className="mt-2 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium"
            >
              
            </button>
          </div>
        )}

        {/*  */}
        {extractedRegions.length > 0 && (
          <div className="space-y-4">
            <h4 className="text-sm font-medium text-gray-900"></h4>
            {extractedRegions.map((region) => (
              <div key={region.id} className="p-3 border border-gray-200 rounded-lg">
                <div className="flex items-center justify-between mb-2">
                  <label className="text-sm font-medium text-gray-700">
                    {region.name}
                  </label>
                  {region.confidence && (
                    <span className="text-xs text-gray-500">
                      : {region.confidence.toFixed(1)}%
                    </span>
                  )}
                </div>
                <textarea
                  value={region.text}
                  onChange={(e) => handleRegionEdit(region.id, e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded text-sm focus:ring-1 focus:ring-blue-500 focus:border-blue-500"
                  rows={2}
                  placeholder="..."
                />
              </div>
            ))}
          </div>
        )}

        {/*  */}
        {!fullText && !isProcessing && (
          <div className="text-center py-12 text-gray-500">
            <svg className="w-16 h-16 mx-auto mb-4 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
            </svg>
            <p className="text-lg font-medium mb-2">
              {fileType === 'application/pdf' ? 'PDF' : ''}
            </p>
            <p className="text-sm">
              {fileType === 'application/pdf' 
                ? 'PDF'
                : 'OCR'
              }
            </p>
            {template && (
              <p className="text-sm text-blue-600 mt-2">
                 "{template.originalname}" 
              </p>
            )}
            {!template && (
              <p className="text-sm text-orange-600 mt-2">
                
              </p>
            )}
            {fileType === 'application/pdf' && (
              <div className="mt-4 p-3 bg-yellow-50 border border-yellow-200 rounded-lg text-left">
                <h4 className="text-sm font-medium text-yellow-800 mb-2"> PDF</h4>
                <ul className="text-xs text-yellow-700 space-y-1">
                  <li> <strong>PDF</strong></li>
                  <li> <strong>PDF</strong></li>
                  <li> <strong>PDF</strong>OCR</li>
                  <li> <strong></strong>PDFJPG/PNG</li>
                </ul>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

export default OCRProcessor; 